ul{list-style-type: none; margin: 0; padding: 0;}
a{text-decoration: none;}
img{display: block; border: 0px none;}

#header_section{
    height:44px; background-color:#fff; position:fixed; left: 0; top: 0; right:0; z-index: 10000;
    border-bottom: 1px solid #ccc;
    #nav{
        max-width:980px; margin: 0 auto; text-align: center;
        ul{
            display: flex; height: 44px;
            justify-content:center; align-items:center;
            gap: 10px;
            a{
                display: block; line-height: 14px; padding:6px 24px; letter-spacing: 0.1em;
                color: #222; font-size: 14px; font-weight: bold;
                &:hover{
                    color: #09f;
                }
            }
        }
    }
}
// ----------------- Header --------------------------------------
#main_content{
    .main_visual{
        width: 100%; height: 774px;
        .inner{
            max-width:1920px; width: 100%; height: 100%; margin: 0 auto;
            img{
                width: 100%; height: 100%; object-fit:cover; 
            }
        }
    }
    //--------------------- MainVisual ----------------------------
    .title{
    font-size: 18px; margin-bottom: 10px; padding-bottom: 10px; border-bottom: 1px dotted #d9d9d9;
    }
    #contents{
        width: 100%; max-width:980px; margin: 0 auto;
        .now_movies{
            margin: 40px 0px 60px;
            .movies_wrap{
                padding-top: 20px; display: flex; flex-flow:row wrap;
                justify-content:space-between; gap: 15px;
                li{
                    flex:1 1 0;
                    img{
                        width: 100%; height: 100%; object-fit:cover;
                    }
                }
            }
        }
        // --------------------- NowMovies ------------------------------
        .special{
            margin-bottom: 34px;
            .special_wrap{
                display: flex; justify-content:space-between; padding: 16px 0px;
            }
        }
        .evt{
            margin-bottom: 34px;
            .evt_wrap{
                width:980px; height: 512px; display: grid; gap:11px; // position: relative;
                grid-template-rows:repeat(2,250px);
                grid-template-columns:183px 183px 92px 183px 295px; // 183px 183px 103px 112px 295px
                li{
                    height:250px;
                }
                .e1{width: 183px;
                    grid-row:1/2;
                    grid-column:1/2;
                }
                .e2{width: 183px;
                    grid-row:1/2;
                    grid-column:2/3;
                }
                .e3{width: 286px;
                    grid-row:1/2;
                    grid-column:3/5;
                }
                .e4{width: 295px; height: 511px;
                    grid-row:1/2;
                    grid-column:5/6;
                } // position: absolute; top: 0; right: 0;
                .e5{width:478px;
                    grid-row:2/3;
                    grid-column:1/4;
                }
                .e6{width: 183px;
                    grid-row:2/3;
                    grid-column:4/5;}
            }
        }
        .direct_btns{
            margin-bottom: 150px; display: flex; justify-content:space-between;
        }
    }
}

#evt_bx{
    padding-top: 100px; display:flex; justify-content:center; align-items:center;
}